<template>
<div class="slider2-block">
    <div class="container slider2-content">
      <div class="slider2-titles">
        <h4>Самые покупаемые курсы</h4>
        <h3>Популярные курсы</h3>
      </div>
      <div class="slider2">
        <div class="slider2-track" :style="{ 'margin-left': '-' + (75 * this.currentIndex) + '%'}">
            <div class="slider2-item" v-for="slide in $store.state.slider2" >
              <img :src="require(`../assets/images/${slide.bg}.png`)" alt="">
              <div class="item-categoria" :style="{backgroundColor: slide.backgroundColor}">
                 <p>{{slide.categoria}}</p>
              </div>
              <div class="course-info">
                <h3>{{slide.title}}</h3>
                <div class="course-rating2">
                  <div class="level">
                    <div class="icon-level-wrapper">
                      <div class="icon-level"></div>
                    </div>
                    <p>{{slide.level}}</p>
                  </div>
                  <div class="people">
                    <div class="people-icon-wrapper">
                      <div class="icon-people"></div>
                    </div>
                    <p>{{slide.people}}</p>
                  </div>
                  <div class="rating">
                    <div class="rating-icon-wrapper">
                      <div class="icon-rating" @click="likeStar"></div>
                    </div>
                    <p>{{slide.rating}}</p>
                  </div>
                 </div>
                 <div class="course-extra-info">
                    <div class="teacher">
                        <img :src="require(`../assets/images/${slide.avatar}.png`)" alt="">
                         <div :class="'teacher-info ' + slide.class">
                        <p class="name">{{slide.name}}</p>
                        <p class="job">{{slide.job}}</p>
                      </div>
                    </div>
                    <div class="price">{{slide.price}}</div>
                 </div>
              </div>
              <div class="slide-like">
                  <div class="icon-like" @click="likeCourse">
                  </div>
               </div>
            </div>
        </div>
      </div>
      <div class="slider2-controls">
          <sliderControl childclass="prev">
            <div class="icon-prev" @click="prevSlide">
            </div>
          </sliderControl>
          <sliderControl childclass="next" @click="nextSlide">
            <div class="icon-next">
            </div>
          </sliderControl>
        </div>
    </div>
</div>
</template>


<script>

import sliderControl from './sliderControl.vue';

export default{
components: {
  sliderControl
},
    data(){
        return {
          currentIndex: 0
        }
    },
    methods: {
       likeCourse(){
    if(event.target.classList.contains('red')){
        event.target.classList.remove('red')
    }else{
       event.target.classList.add('red')
    }
  },
         prevSlide(){
    if(this.currentIndex > 0){
      this.currentIndex--;
      console.log(this.currentIndex);
    }
  } ,
  nextSlide(){
    if(this.currentIndex >= document.querySelectorAll('.slider1-item').length - 3){
      this.currentIndex = 0;
      console.log(this.currentIndex);
    }else{
      this.currentIndex++;
      console.log(this.currentIndex);
    }
  }
    }
}
</script>

<style lang="scss">
@import '../scss/components/slider2.scss';
</style>