import { createRouter, createWebHashHistory } from "vue-router";

// import { App } from "vue";
import homePage from './components/pages/home-page.vue';
import pageTeacher from './components/pages/page-teacher.vue';
import pageCourse from './components/pages/page-course.vue'

export default createRouter({
    history: createWebHashHistory(),
    routes: [
        {path: '/#homePage', component: homePage, alias: '/'},
        {path: '/pageTeacher', component: pageTeacher},
        {path: '/pageCourse', component: pageCourse}
    ]
})